@mixin mobile {
 @media screen and (max-width: 480px) {
  @content;
 }
}

@mixin tablet {
 @media screen and (max-width: 7680px) {
  @content;
 }
}
@mixin laptop {
 @media screen and (max-width: 1024px) {
  @content;
 }
}





.form-container {
 background-color: #a7bcff;
 height: 100vh;
 display: flex;
 align-items: center;
 justify-content: center;

 .form-wrapper {
  background-color: white;
  padding: 20px 60px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;

  .logo {
   color: #5d5b8d;
   font-weight: bold;
   font-size: 2rem;

  }

  .title {
   color: #5d5b8d;
   font-size: 1rem;
  }

  form {
   display: flex;
   flex-direction: column;
   gap: 15px;

   input {
    padding: 15px;
    border: none;
    border-bottom: 1px solid #a7bcff;
    width: 80%;

    &::placeholder {
     color: rgb(176, 176, 176);
    }

   }

   button {
    background-color: #7b96ec;
    color: white;
    padding: 10px;
    font-weight: bold;
    border: none;
    cursor: pointer;
   }

   label {
    display: flex;
    align-items: center;
    gap: 10px;
    color: #8da4f1;
    font-size: 1rem;
    cursor: pointer;

    img {
     width: 32px;
    }

   }

  }
  p {
   color: #5d5b8d;
   font-size: 0.85rem;
   margin-top: 0.85rem;
  }

 }

}


.home {
 background-color: #a7bcff;
 height: 100vh;
 display: flex;
 align-items: center;
 justify-content: center;

 .container {
  border: 1px solid white;
  border-radius: 10px;
  width: 65%;
  height: 80%;
  display: flex;
  overflow: hidden;

  @include tablet {
   width: 90%;
  }

  .sidebar{
   flex: 1;
   background-color: #3e3c61;
   position: relative;
   .navbar{
    display: flex;
    align-items: center;
    background-color: #2f2d52;
    height: 50px;
    padding: 10px;
    justify-content: space-between;
    color: #ddddf7;

    .logo {
     font-weight: bold;

    }

    .user {
     display: flex;
     gap: 10px;

     img {
      background-color: #ddddf7;
      height: 24px;
      width: 24px;
      border-radius: 50%;
      object-fit: cover;
     }

     button {
      background-color: #5d5b8d;
      color: #ddddf7;
      font-size: 0.85rem;
      border: none;
      cursor: pointer;
      @include tablet {
       position: absolute;
       bottom: 20px;
      }
     }

    }



   }

              .search {
               border-bottom: 1px solid gray;
       
               .search-form {
                padding: 10px;
                input {
                 background-color: transparent;
                 border: none;
                 color: white;
                 outline: none;

                 &::placeholder {
                  color:lightgrey;
                 }
                }
               }
              }
       
              .user-chat {
               padding: 10px;
               display: flex;
               align-items: center;
               gap: 10px;
               color: white;
               cursor: pointer;

               &:hover{
                background-color: #2f2d52;
               }

               img {
                width: 50px;
                height: 50px;
                border-radius: 50%;
                object-fit: cover;
               }

               .user-chat-info{
                span {
                 font-size: 1.2rem;
                 font-weight: 500;
                }
                p {
                 font-size: 1rem;
                 color: lightgray;
                }
               }
              }

  }
  .chat{
   flex: 2;

   .chat-info {
    height: 50px;
    background-color: #5d5b8d;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    color: lightgray;
   }

   .chat-icons {
    display: flex;
    gap: 10px;

    img{
     height: 24px;
     cursor: pointer;
    }

   }

   .messages {
    background-color: #ddddf7;
    padding: 10px;
    height: calc(100% - 160px);
    overflow: scroll;

    .message {
     display: flex;
     gap: 20px;

     .message-info {
      display: flex;
      flex-direction: column;
      color: gray;
      font-weight: 300;
      margin-bottom: 20px;

      img{
       width: 40px;
       height: 40px;
       border-radius: 50%;
       object-fit: cover;
      }
     }

     .message-content {
      max-width: 80%;
      display: flex;
      flex-direction: column;
      gap: 10px;

      p {
       background-color: white;
       padding: 10px 20px;
       border-radius: 0px 10px 10px 10px;
       max-width: max-content;
      }

      img{
       width: 50%;

      }
     }

     &.owner {
      flex-direction: row-reverse;
      align-items: flex-end;
      p {
       background-color: #8da4f1;
       color: white;
       border-radius: 10px 0px 10px 10px;
      }

     }

    }

   }

   .input {
    height: 50px;
    background-color: white;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    input {
     width: 100%;
     border: none;
     outline: none;
     font-size: 1.4rem;

     &::placeholder{
      color: lightgray;
     }
    }

    .send {
     display: flex;
     align-items: center;
     gap: 10px;

     img {
      height: 24px;
      cursor: pointer;
     }

     button {
      border: none;
      padding: 10px 15px;
      color: white;
      background-color: #8da4f1;
      
     }

    }

   }

  }

 }
}